import React, { useMemo } from "react";
import tinyImg from "../../images/Tiny_Home.png";
import tiny2Img from "../../images/Tiny_Process.jpg";
import zanelliImg from "../../images/Zanelli_Home.png";
import bakersLaneImg from "../../images/BakersLane_Home.jpg";
import seltzerImg from "../../images/Seltzer_Home.jpg";
import vivaImg from "../../images/viva/viva_p7.png";
import { GatsbyImage } from "gatsby-plugin-image";
import HeroSection from "./heroSection";
import { Link } from "gatsby";
import ProductDesc from "../product/productDesc";
import { projectsData } from "../../data/projectsData";

// https://stackoverflow.com/questions/51349801/querying-all-images-in-folder-using-graphql

const products = { ...projectsData };
products.seltzer.imgSrc = seltzerImg;
products.tiny.imgSrc = tinyImg;
products.tiny.imgSrcList = [tiny2Img, tiny2Img];
products.zanelli.imgSrc = zanelliImg;
products.bakersLane.imgSrc = bakersLaneImg;
products.viva.imgSrc = vivaImg;

export default function HeroProductOverview({ isPage }) {
  const filteredProducts = useMemo(() => {
    return Object.values(products).filter((e) => e.pages.includes(isPage));
  }, [isPage]);

  return (
    <>
      {filteredProducts.map((elem, idx) => {
        const prodDesc = <ProductDesc elem={elem} />;
        const prodImg = (
          <img
            alt="Lane7"
            src={elem.imgSrc}
            width={"100%"}
            className="d-inline-block align-text-top"
          />
        );
        return (
          <Link
            key={elem.id}
            to={elem.url}
            className="nav-link text-dark bg-white pr-2 align-self-center"
            aria-current="page"
          >
            <div className="py-2 px-4" key={elem.id}>
              {!elem.isDual ? (
                <>
                  <div className="row fw-bold align-items-center">
                    <HeroSection idx={idx} desc={prodDesc} ima={prodImg} />
                  </div>
                </>
              ) : (
                <div className="row">
                  {elem.imgSrcList.map((imgSrc, idx) => {
                    return (
                      <div className="col" key={idx}>
                        <img
                          alt="Lane7"
                          src={imgSrc}
                          width={"100%"}
                          className="d-inline-block align-text-top"
                        />
                      </div>
                    );
                  })}
                </div>
              )}
            </div>
          </Link>
        );
      })}
    </>
  );
}

