import * as React from "react";

const col4 = 4;
const col8 = 8;
const HeroSection = ({ idx, desc, ima, children }) => {
  const p1 = { size: 0, elem: null };
  const p2 = { size: 0, elem: null };

  if (idx % 2 === 0) {
    p1.size = col4;
    p1.elem = desc;
    p2.size = col8;
    p2.elem = ima;
  } else {
    p2.size = col4;
    p2.elem = desc;
    p1.size = col8;
    p1.elem = ima;
  }
  return (
    <>
      <div className={`col-${p1.size} text-center`}>{p1.elem}</div>
      <div className={`col-${p2.size} text-center`}>{p2.elem}</div>
    </>
  );
};

export default HeroSection;
