import * as React from "react";
import BlogOverviewItem from "./blogOverviewItem";

const blogItems = [
  {
    id: "blog-1",
    title: "COMPANY UPDATES",
    desc: "World, meet LANE7",
  },
  {
    id: "blog-2",
    title: "THOUGHTS",
    desc: "Universe, take notes. We're manifesting our perfect projects",
  },
  {
    id: "blog-3",
    title: "THOUGHTS",
    desc: "Can we do the strategy ourselves? In defense of the doing the whole shebang.",
  },
];

const BlogOverview = ({ pageTitle, children }) => {
  return (
    <div className="text-center bg-dark text-white">
      <div className="row">
        <div className="col-12 ">
          <p className="p-2 pt-4">THE LANE NEWS</p>
          <p className="fs-2 p-2">The Latest from Lane7</p>
        </div>
        <div className="row">
          {blogItems.map((item, idx) => (
            <BlogOverviewItem key={item.id} item={item} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default BlogOverview;
