import * as React from "react";
import Layout from "../components/layout";
import HeroAnimation from "../components/home/heroAnimation";
import HeroProductOverview from "../components/home/heroProductOverview";
import BlogOverview from "../components/home/blogOverview";
import lane7IntroVideo from "../images/Green.mp4";
import weAnimateVideo from "../images/WEANIMATE.mp4";

const heroStyles = {
  fontSize: "6em",
};

const IndexPage = () => {
  return (
    <Layout pageTitle="Home Page">
      <HeroAnimation>
        <div className="">
          <video autoPlay loop muted>
            <source src={weAnimateVideo} type="video/mp4" />
          </video>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <div className="text-center p-2 vh-50">
          <h1 className="p-4 h1-size">Welcome to LANE7</h1>
          <p className="fs-1 p-4">
            An independent agency dedicated to the craft of advertising
          </p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <div className="text-center bg-dark text-white vh-50">
          <p className="fs-2 py-4">FEATURED WORK</p>
          <p className="fs-3 py-4">Concepts to Ideas to Brands Animation</p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <HeroProductOverview isPage="index" />
      </HeroAnimation>
      <BlogOverview />
    </Layout>
  );
};

export default IndexPage;

export const Head = () => <title>Home Page</title>;
